@import "https://fonts.googleapis.com/css2?family=Raleway:wght@700&family=Roboto:wght@400;500;700;900&display=swap";
*, :before, :after {
  box-sizing: border-box;
}

body {
  margin: 0;
}

a, button {
  cursor: revert;
}

ol, ul, menu {
  list-style: none;
}

img {
  max-width: 100%;
  max-height: 100%;
}

table {
  border-collapse: collapse;
}

input, textarea {
  -webkit-user-select: auto;
}

textarea {
  white-space: revert;
}

meter {
  -webkit-appearance: revert;
  -ms-appearance: revert;
  appearance: revert;
}

:where(pre) {
  all: revert;
}

::-ms-input-placeholder {
  color: unset;
}

::placeholder {
  color: unset;
}

::marker {
  content: initial;
}

:where([hidden]) {
  display: none;
}

:where([contenteditable]:not([contenteditable="false"])) {
  -moz-user-modify: read-write;
  -webkit-user-modify: read-write;
  overflow-wrap: break-word;
  -webkit-line-break: after-white-space;
  -webkit-user-select: auto;
}

:where([draggable="true"]) {
  -webkit-user-drag: element;
}

:where(dialog:modal) {
  all: revert;
}

:root {
  --text-font-family: Roboto;
  --menu-select-color: #000;
  --select-color: #b92f2c;
  --title-color: #000;
  --description-color: #b92f2c;
}

.section {
  width: 100%;
  max-width: 1280px;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.container {
  width: 100%;
  max-width: 1216px;
  margin: 0 auto;
  padding: 0;
}

.gallery {
  width: 100%;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
  gap: 16px;
  padding: 0;
  display: flex;
}

.gallery__items {
  width: 384px;
  cursor: pointer;
  border-radius: 5px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, .16), 0 3px 6px rgba(0, 0, 0, .23);
}

.gallery__items:hover, .gallery__items:focus {
  border: 5px solid #b92f2c;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  scale: .95;
}

.gallery__items__img img {
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}

.gallery__items__details {
  height: 100px;
  flex-direction: column;
  justify-content: flex-start;
  margin-top: 10px;
  margin-left: 10px;
  display: flex;
}

.gallery__items__details--title {
  color: var(--title-color);
  font-family: var(--text-font-family);
  text-transform: uppercase;
  margin-top: 10px;
  margin-bottom: 10px;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
}

.gallery__items__details--genres {
  color: var(--select-color);
  font-family: var(--text-font-family);
  margin-top: 0;
  margin-bottom: 0;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
}

.loader {
  height: 10px;
  width: 140px;
  background-image: linear-gradient(#fff 10px, rgba(0, 0, 0, 0) 0), linear-gradient(#fff 10px, rgba(0, 0, 0, 0) 0), linear-gradient(#fff 10px, rgba(0, 0, 0, 0) 0), linear-gradient(#fff 10px, rgba(0, 0, 0, 0) 0);
  background-position: 0 0, 40px 0, 70px 0, 100px 0;
  background-repeat: no-repeat;
  background-size: 10px;
  margin: 10px auto;
  animation: pgfill 1s linear infinite;
  display: block;
}

@keyframes pgfill {
  0% {
    background-image: linear-gradient(#fff 10px, rgba(0, 0, 0, 0) 0), linear-gradient(#fff 10px, rgba(0, 0, 0, 0) 0), linear-gradient(#fff 10px, rgba(0, 0, 0, 0) 0), linear-gradient(#fff 10px, rgba(0, 0, 0, 0) 0);
  }

  25% {
    background-image: linear-gradient(#ff3d00 10px, rgba(0, 0, 0, 0) 0), linear-gradient(#fff 10px, rgba(0, 0, 0, 0) 0), linear-gradient(#fff 10px, rgba(0, 0, 0, 0) 0), linear-gradient(#fff 10px, rgba(0, 0, 0, 0) 0);
  }

  50% {
    background-image: linear-gradient(#ff3d00 10px, rgba(0, 0, 0, 0) 0), linear-gradient(#ff3d00 10px, rgba(0, 0, 0, 0) 0), linear-gradient(#fff 10px, rgba(0, 0, 0, 0) 0), linear-gradient(#fff 10px, rgba(0, 0, 0, 0) 0);
  }

  75% {
    background-image: linear-gradient(#ff3d00 10px, rgba(0, 0, 0, 0) 0), linear-gradient(#ff3d00 10px, rgba(0, 0, 0, 0) 0), linear-gradient(#ff3d00 10px, rgba(0, 0, 0, 0) 0), linear-gradient(#fff 10px, rgba(0, 0, 0, 0) 0);
  }

  100% {
    background-image: linear-gradient(#ff3d00 10px, rgba(0, 0, 0, 0) 0), linear-gradient(#ff3d00 10px, rgba(0, 0, 0, 0) 0), linear-gradient(#ff3d00 10px, rgba(0, 0, 0, 0) 0), linear-gradient(#ff3d00 10px, rgba(0, 0, 0, 0) 0);
  }
}

