  line-height: 40px;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 8px;
  right: 8px;
}

.gallery__close-button:hover {
  cursor: pointer;
  filter: progid:DXImageTransform. Microsoft. gradient(startColorstr= "#ffc99e", endColorstr= "#ffc99e", GradientType= 1);
  color: #ff6c00;
  background: linear-gradient(138deg, #ffc99e 0%, #ffe9d7 15%, rgba(255, 255, 255, 0) 50%, #ffe9d7 85%, #ffc99e 100%);
  border-color: #ff6c00;
  animation: rotating-circle 2s infinite;
}

@keyframes rotating-circle {
  0% {
    transform: rotate(0deg);
  }

  50% {
    transform: rotate(180deg);
  }

  100% {
    transform: rotate(-180deg);
  }
}

#return-to-top {
  width: 50px;
  height: 50px;
  -webkit-transition: all .3s linear;
  -o-transition: all .3s ease;
  background: rgba(0, 0, 0, .7);
  border-radius: 35px;
  text-decoration: none;
  transition: all .3s;
  display: none;
  position: fixed;
  bottom: 20px;
  right: 20px;
}

#return-to-top i {
  color: #fff;
  -o-transition: all .3s ease;
  margin: 0;
  font-size: 19px;
  transition: all .3s;
  position: relative;
  top: 13px;
  left: 16px;
}

#return-to-top:hover {
